@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Lato', sans-serif;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a {
  text-decoration: none;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    padding-bottom: 100px;
}
article:hover {
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
}
h1 {
    margin-bottom: 40px;
}
article {
    width: 500px;
    height: 70px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    background-color: #fff;
}
.login {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.7);
    display: none;
}
input {
    width: 40% !important;
    margin-bottom: 20px;
}
#close {
    background-color: transparent;
    border: none;
    color: #fff;
    font-size: 30px;
    position: absolute;
    top: 20px;
    right: 30px;
}


.width {
    width: 50%;
}
.width input {
    width: 100% !important;
}
.medView {
    width: 50%;
    border: 2px solid #000;
    margin-top: 30px;
    padding: 20px;
    padding-left:30px;
}
#medicine-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.team-member {
    width: 50%;
    border: 2px solid #000;
    margin-top: 30px;
    padding: 20px;
    padding-left:30px;
}
#team-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#product {
    height: 300px;
    border: 2px solid #000;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    width: 50%;
    overflow: scroll;
}
.addMedicine {
    border: 1px solid #000;
    width: 100%;
    height: 100px !important;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    margin-bottom: 10px;
}
.quantity {
    margin-bottom: 5px;
    width: 100px !important;
}
.insideMed {
    display: flex;
    justify-content: space-between;
}
#medWrap {
    border: 1px solid #000;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
}
#orders {
    display: flex;
    align-items: center;
    flex-direction: column;
}
