#product {
    height: 300px;
    border: 2px solid #000;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    width: 50%;
    overflow: scroll;
}
.addMedicine {
    border: 1px solid #000;
    width: 100%;
    height: 100px !important;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    margin-bottom: 10px;
}
.quantity {
    margin-bottom: 5px;
    width: 100px !important;
}